.Footer__main {
  height: 60vh;
  display: flex;
  // font-family: "Poppins", sans-serif;
  font-family: "Josefin Sans", sans-serif;

  padding: 1rem 2rem;
  background-color: #222222;
  color: #fff;
  justify-content: space-between;
  align-items: center;

  &--content {
    width: 50%;
    padding: 0.5rem 1rem;

    h1 {
      font-size: 2.75rem;
      color: #fff;
    }
    p {
      margin-top: 1.25rem;
      font-size: 2.25rem;
      text-align: justify;
      font-weight: 200;
      .sp {
        background-color: transparent;
        transition: background-color 0.5s ease;
        border-radius: 0.5rem;
        padding: 0rem 0.25rem;
      }
    }
  }
  &--form {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;

    h1 {
      font-size: 2rem;
      font-weight: 200;
      margin-bottom: 1.5rem;
      padding: 1rem 4rem;
      font-family: "Afacad", sans-serif;
      strong {
        color: rgb(62, 62, 62);
        background-color: rgb(79, 248, 73);
        padding: 0rem 0.75rem;
      }
    }
  }
}
input[type="email"],
button[type="submit"],
button[type="submit"]:focus,
input[type="email"]:focus {
  outline: none;
  border: none;
  background: none;
  font-family: "Poppins", sans-serif;
}
#email--form {
  padding: 1rem 1.5rem;
  width: 50%;
  background-color: rgb(47, 47, 47);
  font-size: 1.5rem;
  color: #fff;
}
#sme {
  padding: 1rem 1.5rem;
  font-weight: 600;
  background-color: #75fd2c;
  cursor: pointer;
  color: rgb(255, 255, 255);
  font-size: 1.5rem;
  transition: 0.5s ease;
  &:hover {
    background-color: #1f1e1e;
  }
}

// anims
.Footer__main--content:hover p .sp {
  background-color: rgb(66, 66, 66);
}

