.header__main {
  display: flex;
  width: 100%;
  height: 100vh;
  // font-family: "Poppins", sans-serif;
  font-family: "Josefin Sans", sans-serif;
  // font-family: "Playfair Display", serif;

  &--left {
    width: 40%;
    padding: 1rem 2rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    &--logo {
      color: #7c7c7c;
      #id--image {
        width: 2.5rem;
        height: 2.5rem;
      }
    }
    p {
      font-size: 2rem;
    }
    &--content {
      width: 100%;
      h1 {
        font-family: "Poppins", sans-serif;

        font-size: 6rem;
        color: #434343;
        animation: text_load_anim 0.75s ease;
      }
      p {
        animation: text_load_anim 0.75s;
      }
      .coffee {
        font-style: normal;
        background-color: #fad961;
        background-image: linear-gradient(90deg, #fad961 0%, #f76b1c 100%);
        background-clip: text;
        color: transparent;
      }
    }
    &--form {
      input[type="email"],
      button[type="submit"],
      button[type="submit"]:focus,
      input[type="email"]:focus {
        outline: none;
        border: none;
        background: none;
        font-family: "Poppins", sans-serif;
      }
      #email--form {
        padding: 1rem 1.5rem;
        width: 50%;
        background-color: rgb(81, 81, 81);
        font-size: 1.5rem;
      }
      #sme {
        padding: 1rem 1.5rem;
        font-weight: 600;
        background-color: #46ff32;
        cursor: pointer;
        color: rgb(255, 255, 255);
        font-size: 1.5rem;
        transition: 0.5s ease;
        &:hover {
          background-color: #1f1e1e;
        }
      }

      margin-bottom: 4.5rem;
    }
  }
  &--right {
    width: 60%;
    padding: 1rem 2rem;
    &--container {
      height: 100%;
      border-radius: 2rem;
      background: url("https://c0.wallpaperflare.com/preview/403/838/400/interior-tech-technology-coffee.jpg");
      background-size: cover;
    }
  }
}

@keyframes text_load_anim {
  0% {
    opacity: 0.25;
    transform: translateY(2rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0rem);
  }
}

@media only screen and (max-width: 1200px) {
  .header__main {
    &--left {
      &--content {
        h1 {
          font-size: 4rem;
        }
      }
      &--form {
        width: 100%;
        display: flex;
        flex-direction: row;
        zoom: 0.8;
      }
    }
  }
}

