.offer__main {
  height: 80vh;
  padding: 1rem 2rem;
  font-family: "Josefin Sans", sans-serif;
  margin-bottom: 4rem;

  &--content {
    height: 100%;
    h1 {
      font-size: 2rem;
    }

    &--cards {
      height: 100%;
      display: flex;
      justify-content: space-around;
      align-items: center;
      column-gap: 1.5rem;
      animation: cardLoadAnim 1s ease;
      display: flex;
    }
  }
}

.offer--card {
  height: 70%;
  width: 22rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem 1.25rem;
  background: #2a2a2a;
  border-radius: 1.25rem;
  transition: 0.5s all;
  box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px,
    rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
  &:hover {
    box-shadow: rgba(29, 255, 51, 0.877) 0px 2px 4px 0px,
      rgba(21, 224, 255, 0.788) 0px 2px 16px 0px;
  }

  .ccon {
    background-clip: text;
    background-color: #fa8bff;
    transition: 0.5s all;
    background-image: linear-gradient(
      45deg,
      #fa8bff 0%,
      #2bd2ff 52%,
      #2bff88 90%
    );

    &:hover {
      background-clip: text;
      background-size: 400% 400%;
      animation: gradient 2s ease infinite;
    }

    color: transparent;

    h1 {
      font-size: 7.5rem;
    }
    p {
      font-size: 1.25rem;
      margin-bottom: 0.25rem;
      text-align: center;
      color: #fff;
    }
  }
}

@keyframes cardLoadAnim {
  0% {
    opacity: 0;
    transform: translateX(-2rem);
  }
  100% {
    opacity: 1;
    transform: translateX(0rem);
  }
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@media only screen and (max-width: 1000px) {
  .offer__main {
    height: auto;
  }
  .offer__main--content--cards {
    display: flex;
    flex-direction: column;
    row-gap: 1.25rem;
  }
  .offer--card {
    height: 20rem;
  }
}

@media only screen and (max-width: 425px) {
  .offer__main {
  }
  .offer--card {
    zoom: 0.8;
  }
  .ccon {
    h1 {
      font-size: 0.5rem;
    }
    p {
      font-size: 1.25rem;
      margin-bottom: 0.25rem;
      text-align: center;
      color: #fff;
    }
  }
  .offer__main--content--cards {
    display: flex;
    flex-direction: column;
    row-gap: 1.25rem;
  }
}
