.info__main {
  font-family: "Josefin Sans", sans-serif;
  padding: 1.5rem 2rem;
  background: rgba(27, 27, 27, 0.984);
  color: #fff;
  h1 {
    font-size: 2rem;
  }
  p {
    font-size: 1.25rem;
  }
  li {
    list-style: square;
  }
  .info--1 {
    padding: 1rem 1.25rem;
    margin-top: 1.25rem;
    width: 80%;
    display: flex;
    flex-direction: column;
    row-gap: 1.25rem;
  }
}

::selection {
  background-color: #01c560; /* Set the desired highlight color */
  color: #ffffff; /* Set the text color when selected */
}
