.reviews__main {
  height: 60vh;
  max-width: 100%;
  margin-bottom: 3.5rem;
  font-family: "Josefin Sans", sans-serif;
  padding: 1rem 2rem;
}
.review__box {
  max-height: 100%;
  max-width: 100%;
  display: flex;
  align-items: center;
  display: flex;
  column-gap: 2.25rem;
}
.rev--con {
  max-width: 100%;
  height: 100%;
}
.review__box {
  width: 100%;
  min-height: 100%;

  &--card {
    width: 100%;
    min-height: 100%;
    padding: 3.25rem 4.75rem;

    display: flex;
    flex-direction: column;
    row-gap: 1.25rem;
    background: rgba(245, 245, 245, 0.867);
    &--review {
      font-size: 1.25rem;
      text-align: center;
      font-weight: 400;
      animation: textanim 1.25s;
    }
    &--stars {
      text-align: center;
      animation: textanim 1.25s;
    }
    &--name {
      text-align: center;
      animation: textanim 1.25s;
    }
  }
}
/*
    I've been enjoying midnightVenture's immensely! The content is engaging,
    the design is sleek, and it arrives in my inbox right on time. The
    personal touch and community feel make it a standout. Highly
    recommended!
*/

@keyframes textanim {
  0% {
    opacity: 0;
    transform: translateX(-0.25rem);
  }
  100% {
    opacity: 1;
    transform: translateX(0rem);
  }
}
